"use client";

import React, { useState } from "react";
import Stepper from "@/components/Stepper";
import { Step } from "@/components/Stepper/types";
import { Button } from "@/components/ui/button";

const initialSteps: Step[] = [
  { id: 1, label: "Datos personales", completed: true },
  { id: 2, label: "Datos de empresa", completed: false },
  { id: 3, label: "Verificación", completed: false },
  { id: 4, label: "Confirmación", completed: false },
];

const StepPersonalInformation = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [steps, setSteps] = useState<Step[]>(initialSteps);

  const handleStepChange = (step: number) => {
    setCurrentStep(step);
  };

  const handleNext = () => {
    if (currentStep < steps.length - 1) {
      const newSteps = [...steps];
      newSteps[currentStep].completed = true;
      setSteps(newSteps);
      setCurrentStep(currentStep + 1);
    }
  };

  const handleBack = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1);
    }
  };

  return (
    <section className="bg-[#1249C7] h-screen w-screen">
      <div className="container mx-auto h-full flex items-center justify-center">
        <div className="bg-white rounded-lg w-full max-w-6xl flex gap-8">
          {/* Panel izquierdo */}
          <div className="w-2/5 bg-[#2F6EFF] p-8 text-white">
            <h1 className="text-2xl font-bold">Datos personales</h1>
            <p className="text-md">
              Completa los campos obligatorios para acceder a tu cuenta.
            </p>
          </div>

          {/* Panel derecho */}
          <div className="w-3/5 p-8">
            {/* Stepper */}
            <div className="mb-8">
              <Stepper
                steps={steps}
                currentStep={currentStep}
                onStepChange={handleStepChange}
              />
            </div>

            {/* Formulario */}
            <form className="space-y-6" onSubmit={(e) => e.preventDefault()}>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-1">
                    País<span className="text-red-500">*</span>
                  </label>
                  <select className="w-full p-3 border border-gray-300 rounded-lg">
                    <option>Selecciona</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium mb-1">
                    Razón social<span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder="Completa"
                    className="w-full p-3 border border-gray-300 rounded-lg"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-1">
                    Nombre<span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder="Completa"
                    className="w-full p-3 border border-gray-300 rounded-lg"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium mb-1">
                    Apellido<span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder="Completa"
                    className="w-full p-3 border border-gray-300 rounded-lg"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">
                  Número de teléfono<span className="text-red-500">*</span>
                </label>
                <input
                  type="tel"
                  placeholder="+56 9 1234 5678"
                  className="w-full p-3 border border-gray-300 rounded-lg"
                />
              </div>

              {/* Botones de navegación */}
              <div
                className={`flex justify-between pt-8 ${currentStep === 0 ? "justify-end" : "justify-between"}`}
              >
                {currentStep > 0 && (
                  <Button
                    type="button"
                    onClick={handleBack}
                    variant="outline"
                    className="px-6 py-2"
                    disabled={currentStep === 0}
                  >
                    ← Volver
                  </Button>
                )}
                <Button
                  type="button"
                  onClick={handleNext}
                  className="px-6 py-2"
                  disabled={currentStep === steps.length - 1}
                >
                  Continuar →
                </Button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default StepPersonalInformation;
